<template>
  <div class="products-container">
    <template>
      <v-data-table :headers="headers" :items="usersList" class="elevation-1" locale="es">
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>Usuarios</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">Agregar</v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>
                <v-form ref="form" v-model="valid" @submit.prevent="save" lazy-validation>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="6" sm="6" md="6">
                          <v-text-field v-model="editedItem.username" label="Usuario" :rules="[rules.required, rules.maxUsername]"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                          <v-text-field
                            v-model="editedItem.password"
                            label="Contrasea"
                            :rules="editedIndex === -1 ? [rules.required] : []"
                            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="showPassword ? 'text' : 'password'"
                            @click:append="showPassword = !showPassword"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="6" sm="6" md="6">
                          <v-text-field v-model="editedItem.name" label="Nombre" :rules="[rules.required, rules.maxDesc]"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                          <v-text-field v-model="editedItem.last_name" label="Apellido" :rules="[rules.required, rules.maxDesc]"></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="6" sm="6" md="6">
                          <v-text-field v-model="editedItem.dni" label="DNI"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                          <v-text-field v-model="editedItem.email" label="E-Mail" :rules="[rules.maxDesc]"></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="6" sm="6" md="6">
                          <v-checkbox
                            v-model="editedItem.admin"
                            label="Es Administrador?"
                          ></v-checkbox>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                    <v-btn color="blue darken-1" text type="submit" :disabled="!valid || loading">Guardar</v-btn>
                  </v-card-actions>
                </v-form>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.admin="{ item }">
          <v-simple-checkbox v-model="item.admin" disabled></v-simple-checkbox>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
      </v-data-table>
    </template>
  </div>
</template>

<script>
import Users from "@/apiClient/User";

export default {
  data() {
    return {
      users: Users,
      usersList: [],
      dialog: false,
      headers: [
        {
          text: "Nombre",
          align: "start",
          sortable: true,
          value: "name"
        },
        { text: "Apellido", value: "last_name", sortable: true },
        { text: "Usuario", value: "username" },
        { text: "DNI", value: "dni" },
        { text: "E-Mail", value: "email" },
        { text: "Admin", value: "admin" },
        { text: '', value: 'actions', sortable: false },
      ],
      valid: true,
      loading: false,
      showPassword: false,
      editedIndex: -1,
      editedItem: {
        username: '',
        password: '',
        name: '',
        last_name: '',
        email: '',
        dni: '',
        admin: false,
      },
      defaultItem: {
        username: '',
        password: '',
        name: '',
        last_name: '',
        email: '',
        dni: '',
        admin: false,
      },
      rules: {
          required: value => !!value || 'Campo obligatorio.',
          maxUsername: v => v.length <= 70 || 'Este campo no debe superar los 70 caracteres',
          maxDesc: v => v.length <= 200 || 'Este campo no debe superar los 200 caracteres',
        },
    };
  },
  async created() {
    this.users = new Users()
    await this.getUsers()
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo usuario" : "Editar usuario";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  methods: {
    getUsers(){
      this.users.getUsers()
        .then(res => {
          this.usersList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    editItem(item) {
      this.editedIndex = this.usersList.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.$swal({
        title: 'Ests seguro?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar',
        cancelButtonText: 'No, cancelar',
        reverseButtons: true
      }).then((result) => {
        if (result.value) {
          this.users.deleteUser(item.id).then(res => {
            this.getUsers()
            this.$swal({
              icon: 'success',
              title: res.data.message,
            })
          }).catch(err => {
            this.$swal({
              icon: 'error',
              title: res.data.message,
            })
          })
        }
      })
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.showPassword = false
      });
    },

    async save() {
      this.loading = true
      if(this.$refs.form.validate()){
        if (this.editedIndex > -1) {
          await this.users
            .updateUser(this.editedItem)
            .then(res => {
              this.loading = false
              this.getUsers()
              this.$swal({
                icon: 'success',
                title: res.data.message,
              });
            })
            .catch(err => {
              this.loading = false
              this.$swal({
                icon: 'error',
                title: 'Error',
              });
            });
        } else {
          await this.users
            .createUser(this.editedItem)
            .then(res => {
              this.loading = false
              this.getUsers()
              this.$swal({
                icon: 'success',
                title: res.data.message,
              });
            })
            .catch(err => {
              this.loading = false
              this.$swal({
                icon: 'error',
                title: 'Error',
              });
            });
        }
        this.close();
      }else{
        this.loading = false
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.products-container {
  padding: 2rem 2rem 0 2rem;
}
</style>