import { BasicClient } from '@/utils/httpClient'

export default class Budgets {
  client
  constructor() {
    this.client = new BasicClient();
  }

  getBudgets() {
    return this.client.exec('/budgets', 'GET');
  }
  getBudgetById(id) {
    return this.client.exec(`/budget/${id}`, 'GET');
  }
  createBudget(budget) {
    let params = {
      seller: budget.seller,
      customer: budget.customer,
      address: budget.address,
      city: budget.city,
      total: budget.total,
      products: budget.productsBudget
    }
    return this.client.exec('/budget', 'POST', params)
  }
  updateBudget(budgetId, budget) {
    let params = {
      seller: budget.seller,
      customer: budget.customer,
      address: budget.address,
      city: budget.city,
      total: budget.total,
      products: budget.productsBudget
    }
    return this.client.exec(`/budget/${budgetId}`, 'PUT', params)
  }
  deleteBudget(id) {
    return this.client.exec(`/budget/${id}`, 'DELETE');
  }
}