<template>
  <div>
    <v-navigation-drawer
      v-model="drawer"
      :clipped="$vuetify.breakpoint.lgAndUp"
      app
    >
      <v-list dense>
        <template v-for="item in items">
          <v-list-group
            v-if="item.children && item.hasPermission"
            :key="item.text"
            v-model="item.model"
            :prepend-icon="item.model ? item.icon : item['icon-alt']"
            append-icon=""
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>
                  {{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              v-for="(child, i) in item.children"
              :key="i"
              link
              :to="child.to"
            >
              <v-list-item-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>
                  {{ child.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
          <template v-else>
            <v-list-item
              :key="item.text"
              link
              :to="item.to"
              v-if="item.hasPermission"
            >
              <v-list-item-action>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>
                  {{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
        </template>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      app
      color="blue darken-3"
      dark
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title
        style="width: 300px"
        class="ml-0 pl-4 pointer"
        @click="$router.push('/')"
      >
        <span class="hidden-sm-and-down">Lalo VDP</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu
        bottom
        left
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            icon
            large
            v-bind="attrs"
            v-on="on"
          >
            <v-avatar
              size="32px"
              item
              color="light-blue darken-1"
            >
              <span class="">{{ iniciales }}</span>
            </v-avatar>
          </v-btn>
        </template>

        <v-list>
          <v-list-item @click="editPassDialog = true" class="pointer">
            <v-list-item-title>Cambiar contrasea</v-list-item-title>
          </v-list-item>
          <v-list-item @click="logOut" class="pointer">
            <v-list-item-title>Salir</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
    <v-dialog v-model="editPassDialog" max-width="30rem">
      <v-card>
        <v-card-title>
          <span class="headline">Cambiar contrasea</span>
          <v-spacer></v-spacer>
        </v-card-title>
        <v-card-text>
          <v-form ref="form" v-model="valid" @submit.prevent="save" lazy-validation>
            <v-container>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    id="password"
                    label="Contrasea nueva"
                    name="newpassword"
                    prepend-icon="mdi-lock"
                    v-model="newPassword"
                    :append-icon="showPassword1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :rules="[rules.required]"
                    :type="showPassword1 ? 'text' : 'password'"
                    @click:append="showPassword1 = !showPassword1"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    id="repeat-password"
                    label="Repita su contrasea nueva"
                    name="password2"
                    prepend-icon="mdi-lock"
                    v-model="repeatPassword"
                    :append-icon="showPassword2 ? 'mdi-eye' : 'mdi-eye-off'"
                    :rules="[rules.required]"
                    :type="showPassword2 ? 'text' : 'password'"
                    @click:append="showPassword2 = !showPassword2"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-spacer></v-spacer>
                <v-col cols="3" sm="3" md="3">
                  <v-btn color="primary" large type="submit" class="float-right" :disabled="!valid || loading">Guardar</v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import User from "@/apiClient/User"

export default {
  data: () => {
    return {
      user: User,
      drawer: null,
      editPassDialog: false,
      newPassword: '',
      repeatPassword: '',
      showPassword1: false,
      showPassword2: false,
      valid: true,
      loading: false,
      rules: {
        required: value => !!value || 'Campo obligatorio',
      },
      items: [
        { icon: 'mdi-cart-outline', text: 'Productos', to: 'products', hasPermission: true },
        {
          icon: 'mdi-chevron-up',
          'icon-alt': 'mdi-chevron-down',
          text: 'Remitos',
          hasPermission: true,
          children: [
            { icon: 'mdi-plus', text: 'Crear remito', to: 'budgets-create'},
            { icon: 'mdi-file-document-outline', text: 'Listado de remitos', to: 'budgets'},
          ],
        },
        { icon: 'mdi-truck-outline', text: 'Ordenes', to: 'orders', hasPermission: true },
        { icon: 'mdi-account-multiple', text: 'Usuarios', to: 'users', hasPermission: false }
      ],
      iniciales: ''
    }
  },
  computed: {
    ...mapState['user'],
  },
  created() {
    this.user = new User()
    this.iniciales = `${this.$store.state.user.name.charAt(0)}${this.$store.state.user.last_name.charAt(0)}`
    if(this.$store.state.user.admin){
      this.items[3].hasPermission = true
    }
  },
  methods: {
    ...mapActions({clearState: 'clearState'}),
    logOut(){
      this.clearState()
      this.$router.push('/login')
    },
    async save() {
      this.loading = true
      if(this.$refs.form.validate()){
        if(this.newPassword !== this.repeatPassword) {
          this.$swal({
            icon: 'error',
            title: 'Las contraseas no coinciden',
          })
          this.loading = false
          return
        }
        await this.user.changePass(this.$store.state.user.id, this.newPassword)
          .then(res => {
            this.$swal({
              icon: 'success',
              title: res.data.message,
              text: "Vuelva a iniciar sesin"
            }).then((result) => {
              if (result.value) {
                this.loading = false
                this.logOut()
              }
            })
          })
          .catch(err => {
            this.$swal({
              icon: 'error',
              title: err.message,
            })
            this.loading = false
          })
      }else{
        this.loading = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.pointer {
  cursor: pointer;
}
</style>