const initialState = {
  user: {},
  jwt: ''
}

export default {
  SET_JWT: (state, payload) => {
    state.jwt = payload.jwt
  },
  SET_USER: (state, payload) => {
    state.user = payload
  },
  CLEAR_STATE: (state) => {
    Object.keys(state).forEach(key => {
      state[key] = initialState[key]
    })
  }
}