<template>
  <div class="products-container">
    <template>
      <v-data-table :headers="headers" :items="budgetList" class="elevation-1" locale="es">
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>Remitos</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-btn color="primary" dark class="mb-2" @click="$router.push('/budgets-create')">Agregar</v-btn>
            <v-dialog v-model="dialogDetail" max-width="50rem">
              <v-card>
                <v-card-title>
                  <span class="headline">Detalle de remito</span>
                  <v-spacer></v-spacer>
                  <!-- <v-btn class="ma-2" outlined color="pink">
                    <span>Imprimir</span>
                    <v-icon right>mdi-printer</v-icon>
                  </v-btn> -->
                  <v-btn icon color="pink" @click="printBudget">
                    <v-icon>mdi-printer</v-icon>
                  </v-btn>
                </v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="budgetDetail.customer" label="Cliente" readonly></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field v-model="budgetDetail.address" label="Direccin" readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field v-model="budgetDetail.city" label="Ciudad" readonly></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="budgetDetail.seller" label="Vendedor" readonly></v-text-field>
                      </v-col>
                    </v-row>
                    <v-data-table :headers="detailsHeaders" :items="budgetDetail.details" hide-default-footer class="elevation-1" locale="es">
                      <template slot="body.append">
                        <tr class="pink--text">
                          <th class="title">Total</th>
                          <th class="title"></th>
                          <th class="title"></th>
                          <th class="title"></th>
                          <th class="title"></th>
                          <th class="title">{{ budgetDetail.total }}</th>
                        </tr>
                      </template>
                    </v-data-table>
                  </v-container>
                </v-card-text>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="itemDetail(item)">mdi-eye</v-icon>
          <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
      </v-data-table>
    </template>
  </div>
</template>

<script>
import Budgets from "@/apiClient/Budgets"
import jspdf from 'jspdf'
import moment from 'moment'

export default {
  inject: ['state', 'mutations'],
  data() {
    return {
      budgets: Budgets,
      budgetList: [],
      budgetDetail: [],
      dialogDetail: false,
      headers: [
        {
          text: "Fecha",
          align: "start",
          sortable: true,
          value: "date"
        },
        { text: "Cliente", value: "customer", sortable: true },
        { text: "Direccin", value: "address", sortable: true },
        { text: "Ciudad", value: "city", sortable: true },
        { text: "Vendedor", value: "seller", sortable: true },
        { text: "Total", value: "total", sortable: true },
        { text: '', value: 'actions', sortable: false },
      ],
      detailsHeaders: [
        { text: "Cdigo", value: "product_code"},
        { text: "Producto", value: "product_description"},
        { text: "Talle", value: "product_size"},
        { text: "Precio unitario", value: "price"},
        { text: "Cantidad", value: "quantity"},
        { text: "Subtotal", value: "subtotal"},
      ],
    };
  },
  async created() {
    this.budgets = new Budgets();
    await this.getBudgets()
    this.mutations.setBudget({})
  },
  methods: {
    getBudgets(){
      this.budgets.getBudgets()
        .then(res => {
          this.budgetList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    itemDetail(item){
      this.budgets.getBudgetById(item.id)
        .then(res => {
          this.budgetDetail = res.data
        })
      this.dialogDetail = true
    },
    async editItem(item){
      let param = {} 
      await this.budgets.getBudgetById(item.id)
        .then(res => {
          param = res.data
        })
      this.mutations.setBudget(param)
      this.$router.push('/budgets-update')
    },
    deleteItem(item) {
      this.$swal({
        title: 'Ests seguro?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Eliminar',
        cancelButtonText: 'Cancelar',
        reverseButtons: true
      }).then((result) => {
        if (result.value) {
          this.budgets.deleteBudget(item.id).then(res => {
            this.getBudgets()
            this.$swal({
              icon: 'success',
              title: res.data.message,
            })
          }).catch(err => {
            this.$swal({
              icon: 'error',
              title: res.data.message,
            })
          })
        }
      })
    },
    printBudget(){
      let doc = new jspdf()
      let budget = this.budgetDetail
      
      let img = 'data:image/jpeg;base64,'

      doc.addImage(img, 'jpg', 10, 8, 15, 15)

      // doc.text('Logo', 10, 10)
      doc.text('Lalo Vestir de Pueblo', 10, 30)
      
      doc.setFontSize(10)
      doc.text('OBrien, Buenos Aires', 10, 35)

      doc.text(`N: ${budget.id}`, 150, 10)
      doc.text(`Vendedor: ${budget.seller}`, 150, 15)
      doc.text(`Fecha: ${moment(budget.date).format('DD/MM/YYYY')}`, 150, 20)

      doc.setFontSize(20)
      doc.text(`Presupuesto`, 80, 40)

      doc.setFontSize(12)
      doc.text(`Cliente: ${budget.customer}`, 10, 50)
      doc.text(`Direccin: ${budget.address}`, 10, 55)
      doc.text(`Localidad: ${budget.city}`, 10, 60)

      doc.line(10, 70, 200, 70)
      doc.text('Codigo', 10, 77)
      doc.text('Producto', 30, 77)
      doc.text('Talle', 100, 77)
      doc.text('Cantidad', 130, 77)
      doc.text('Precio unitario', 150, 77)
      doc.text('Subtotal', 180, 77)
      doc.line(10, 80, 200, 80)
      
      doc.setFontSize(8)
      let y = 87
      for(let i = 0; i < budget.details.length; i++){
        doc.text(`${budget.details[i].product_description}`, 30, y)
        doc.text(`${budget.details[i].product_code}`, 10, y)
        doc.text(`${budget.details[i].product_size}`, 100, y)
        doc.text(`${budget.details[i].quantity}`, 130, y)
        doc.text(`${budget.details[i].price}`, 150, y)
        doc.text(`${budget.details[i].subtotal}`, 180, y)
        y += 5
      }
      doc.line(10, y, 200, y)
      let lastLine = y+5
      doc.setFontSize(12)
      doc.text('Total', 150, lastLine)
      doc.text(`${budget.total}`, 180, lastLine)

      doc.save(`remito_${Math.random()}.pdf`)
    }
  }
};
</script>

<style lang="scss" scoped>
.products-container {
  padding: 2rem 2rem 0 2rem;
}
</style>