<template>
  <div class="products-container">
    <template>
      <v-data-table :headers="headers" :items="productList" class="elevation-1" locale="es">
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>Listado de Productos</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">Agregar</v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>
                <v-form ref="form" v-model="valid" @submit.prevent="save" lazy-validation>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.code" label="Cdigo" :rules="[rules.required]"></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field v-model="editedItem.description" label="Descripcin" :rules="[rules.required, rules.maxDesc]"></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.size" label="Talle" :rules="[rules.maxSize]"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.price" label="Precio" :rules="[rules.required]"></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                    <v-btn color="blue darken-1" text type="submit" :disabled="!valid || loading">Guardar</v-btn>
                  </v-card-actions>
                </v-form>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
      </v-data-table>
    </template>
  </div>
</template>

<script>
import Products from "@/apiClient/Products";

export default {
  data() {
    return {
      products: Products,
      productList: [],
      dialog: false,
      headers: [
        {
          text: "Cdigo",
          align: "start",
          sortable: true,
          value: "code"
        },
        { text: "Descripcin", value: "description", sortable: true },
        { text: "Talle", value: "size" },
        { text: "Precio", value: "price" },
        { text: '', value: 'actions', sortable: false },
      ],
      valid: true,
      loading: false,
      editedIndex: -1,
      editedItem: {
        code: "",
        description: "",
        size: "",
        price: 0
      },
      defaultItem: {
        code: "",
        description: "",
        size: "",
        price: 0
      },
      rules: {
          required: value => !!value || 'Campo obligatorio.',
          maxDesc: v => v.length <= 200 || 'Este campo no debe superar los 200 caracteres',
          maxSize: v => v.length <= 15 || 'Este campo no debe superar los 15 caracteres',
        },
    };
  },
  async created() {
    this.products = new Products();
    await this.getProducts()
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo producto" : "Editar producto";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  methods: {
    getProducts(){
      this.products.getProducts()
        .then(res => {
          this.productList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    editItem(item) {
      this.editedIndex = this.productList.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.$swal({
        title: 'Ests seguro?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar',
        cancelButtonText: 'No, cancelar',
        reverseButtons: true
      }).then((result) => {
        if (result.value) {
          this.products.deleteProduct(item.id).then(res => {
            this.getProducts()
            this.$swal({
              icon: 'success',
              title: res.data.message,
            })
          }).catch(err => {
            this.$swal({
              icon: 'error',
              title: res.data.message,
            })
          })
        }
      })
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async save() {
      this.loading = true
      if(this.$refs.form.validate()){
        if (this.editedIndex > -1) {
          await this.products
            .updateProduct(this.editedItem)
            .then(res => {
              this.loading = false
              this.getProducts()
              this.$swal({
                icon: 'success',
                title: res.data.message,
              });
            })
            .catch(err => {
              this.loading = false
              this.$swal({
                icon: 'error',
                title: 'Error',
              });
            });
        } else {
          await this.products
            .createProduct(this.editedItem)
            .then(res => {
              this.loading = false
              this.getProducts()
              this.$swal({
                icon: 'success',
                title: res.data.message,
              });
            })
            .catch(err => {
              this.loading = false
              this.$swal({
                icon: 'error',
                title: 'Error',
              });
            });
        }
        this.close();
      }else{
        this.loading = false
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.products-container {
  padding: 2rem 2rem 0 2rem;
}
</style>