import { BasicClient } from '@/utils/httpClient'

export default class User {
  client
  constructor() {
    this.client = new BasicClient();
  }

  getUsers(){
    return this.client.exec('/users', 'GET')
  }
  getUser() {
    return this.client.exec('/user', 'GET');
  }
  createUser(user){
    let params = {
      username: user.username,
      password: user.password,
      name: user.name,
      last_name: user.last_name,
      email: user.email,
      dni: user.dni,
      admin: user.admin
    }
    return this.client.exec('/user', 'POST', params)
  }
  updateUser(user) {
    let params = {
      username: user.username,
      name: user.name,
      last_name: user.last_name,
      email: user.email,
      dni: user.dni,
      admin: user.admin
    }
    if(user.password) params.password = user.password
    return this.client.exec(`/user/${user.id}`, 'PUT', params)
  }
  changePass(userId, password){
    return this.client.exec(`/user-changepass/${userId}`, 'PUT', {password: password});
  }
  deleteUser(id){
    return this.client.exec(`/user/${id}`, 'DELETE')
  }
}