import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store/state';

import Error404 from '../views/Error404'
import LoginView from '../views/LoginView'
import HomeView from '../views/HomeView'
import ProductsView from '../views/ProductsView'
import BudgetsView from '../views/BudgetsView'
import BudgetsCreateView from '../views/BudgetsCreateView'
import BudgetsUpdateView from '../views/BudgetsUpdateView'
import OrdersView from '../views/OrdersView'
import UsersView from '../views/UsersView'

Vue.use(VueRouter);

const routes = [
  {
    path: '/404',
    name: '404',
    component: Error404,
    meta: {
      requiresAuth: false,
      showMenu: true
    }
  },
  {
    path: '*',
    redirect: '/404'
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView,
    meta: {
      requiresAuth: false,
      showMenu: false,
    }
  },
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      requiresAuth: true,
      showMenu: true
    }
  },
  {
    path: '/products',
    name: 'products',
    component: ProductsView,
    meta: {
      requiresAuth: true,
      showMenu: true
    }
  },
  {
    path: '/budgets',
    name: 'budgets',
    component: BudgetsView,
    meta: {
      requiresAuth: true,
      showMenu: true
    }
  },
  {
    path: '/budgets-create',
    name: 'budgetsCreate',
    component: BudgetsCreateView,
    meta: {
      requiresAuth: true,
      showMenu: true
    }
  },
  {
    path: '/budgets-update',
    name: 'budgetsUpdate',
    component: BudgetsUpdateView,
    meta: {
      requiresAuth: true,
      showMenu: true
    }
  },
  {
    path: '/orders',
    name: 'orders',
    component: OrdersView,
    meta: {
      requiresAuth: true,
      showMenu: true
    }
  },
  {
    path: '/users',
    name: 'users',
    component: UsersView,
    meta: {
      requiresAuth: true,
      showMenu: true
    }
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  let jwt = store.state.jwt

  if(jwt != '' && to.name == 'login'){
    next('/');
  }

  if(to.matched.some(record => record.meta.requiresAuth)){
    if(jwt == '' || (jwt == '' && to.name == '404')){
      next('/login');
    }else{
      next();
    }
  }else{
    next();
  }

});

export default router;
