import { BasicClient } from '@/utils/httpClient'

export default class Products {
  client
  constructor() {
    this.client = new BasicClient();
  }

  getProducts() {
    return this.client.exec('/products', 'GET');
  }
  getProductById(id) {
    return this.client.exec(`/product/${id}`, 'GET');
  }
  createProduct(product) {
    let params = {
      code: product.code,
      description: product.description,
      size: product.size,
      price: product.price
    }
    return this.client.exec('/product', 'POST', params)
  }
  updateProduct(product) {
    let params = {
      code: product.code,
      description: product.description,
      size: product.size,
      price: product.price
    }
    return this.client.exec(`/product/${product.id}`, 'PUT', params)
  }
  deleteProduct(id) {
    return this.client.exec(`/product/${id}`, 'DELETE');
  }
}