import axios from 'axios';
import _ from 'lodash';
import store from '@/store/state';

export { BasicClient }

class BasicClient {
    exec (urlSet, methods, paramsSet, flagForParam) {
        let data;
        let header = {};
        if (paramsSet !== undefined) {
            if (_.includes(['POST', 'PATCH', 'PUT'], methods)) { //Saque el PUT de aca.
                const params = flagForParam ? new FormData() : new URLSearchParams();
                for (let name in paramsSet) {
                    let param = paramsSet[name];
                    if (typeof param === 'object' && !(param instanceof Blob)) {
                        param = JSON.stringify(paramsSet[name]);
                    }
                    params.append(name, param);
                }
                data = { data: params };
            }
            else {
                data = { params: paramsSet };
            }
        }
  
        const Authorization = store.state.jwt ? store.state.jwt : ''
  
  
        header = { headers: { token: Authorization } };
  
        if (flagForParam) {
            header.headers['Content-Type'] = 'multipart/form-data';
        }
  
        return axios.request(_.assignIn({
            url: urlSet,
            method: methods,
            baseURL: 'api', //process.env.VUE_APP_API_URL,
            // headers: header
        }, data, header));
    } 
}
