import { BasicClient } from '@/utils/httpClient'

export default class Orders {
  client
  constructor() {
    this.client = new BasicClient();
  }

  getOrders() {
    return this.client.exec('/orders', 'GET');
  }
  getOrderById(id) {
    return this.client.exec(`/order/${id}`, 'GET')
  }
  createOrder(order) {
    let params = {
      budget_id: order.budget_id,
      status_id: order.order_status,
      comment: order.order_comment ? order.order_comment : '',
      tracking_number: order.order_tracking_number ? order.order_tracking_number : ''
    }
    return this.client.exec('/order', 'POST', params)
  }
  updateOrder(order) {
    let params = {
      status_id: order.order_status,
      comment: order.order_comment ? order.order_comment : '',
      tracking_number: order.order_tracking_number ? order.order_tracking_number : ''
    }
    return this.client.exec(`/order/${order.order_id}`, 'PUT', params)
  }
  deleteOrder(id) {
    return this.client.exec(`/order/${id}`, 'DELETE')
  }
}