<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Login</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-form @submit.prevent="submitLogin" ref="loginform" v-model="valid" lazy-validation>
            <v-card-text>
              <v-text-field
                label="Usuario"
                name="login"
                prepend-icon="mdi-account"
                type="text"
                :rules="[rules.required]"
                v-model="username"></v-text-field>

              <v-text-field
                id="password"
                label="Contrasea"
                name="password"
                prepend-icon="mdi-lock"
                v-model="password"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required]"
                :type="showPassword ? 'text' : 'password'"
                @click:append="showPassword = !showPassword"
              ></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" type="submit" :disabled="!valid || loading">Ingresar</v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Auth from '@/apiClient/Auth'
import User from '@/apiClient/User'

export default {
  name: "Login",
  data() {
    return {
      auth: Auth,
      user: User,
      username: '',
      password: '',
      showPassword: false,
      valid: true,
      loading: false,
      rules: {
          required: value => !!value || 'Campo obligatorio'
      }
    }
  },
  created() {
    this.auth = new Auth();
    this.user = new User();
  },
  methods: {
    async submitLogin(){
      this.loading = true
      if(this.$refs.loginform.validate()){
        await this.auth.login(this.username, this.password).then(async res => {
          const jwt = res.data.token
          const user = res.data.user
          this.$store.dispatch('setJWT', { jwt: jwt})
          this.$store.dispatch('setUser', user)
          this.loading = false
          this.$router.push('/')
        })
        .catch(err => {
          this.loading = false
          this.$swal({
            icon: 'error',
            title: err.response.data.message,
          });
        })
      }else{
        this.loading = false
      }
    }
  }
};
</script>

