<template>
  <div>
    <template>
      <v-card>
        <v-card-title>
          <span class="headline">Crear remito</span>
        </v-card-title>
        <v-form ref="form" v-model="valid" @submit.prevent="save" lazy-validation>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="model.customer" label="Cliente" :rules="[rules.maxChar, rules.required]"></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="model.address" label="Direccin" :rules="[rules.maxChar]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="model.city" label="Ciudad" :rules="[rules.maxChar]"></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="model.seller"
                    label="Vendedor"
                    :rules="[rules.maxChar, rules.required]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-btn color="primary" dark outlined class="mb-2" @click="addProduct">
                  <v-icon left>mdi-plus</v-icon> Agregar producto
                </v-btn>
              </v-row>
              <v-row v-for="(item, index) in model.productsBudget" :key="index">
                <v-col cols="3" sm="3" md="3">
                  <v-combobox
                    v-model="item.product"
                    :items="productsList"
                    item-id="id"
                    :item-text="formatedLabel"
                    label="Seleccione el producto que desea agregar"
                    return-object
                    @input="addProductToBudget(index, item.product)"
                  ></v-combobox>
                </v-col>
                <v-col cols="3" sm="3" md="3">
                  <v-text-field label="Precio" type="number" prefix="$" v-model="item.price" min="0" :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="3" sm="3" md="3">
                  <v-text-field label="Cantidad" type="number" v-model="item.quantity" min="1" :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="2" sm="2" md="2">
                  <v-text-field label="Subtotal" prefix="$" readonly :value="calculateSubtotal(index, item)"></v-text-field>
                </v-col>
                <v-col cols="1" sm="1" md="1">
                  <v-btn icon color="red" class="mt-4" @click="deleteProduct(index)">
                    <v-icon>mdi-trash-can-outline</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
              <v-row>
                <v-spacer></v-spacer>
                <v-col cols="3" sm="3" md="3">
                  <v-text-field label="Total" prefix="$" readonly :value="calculateTotal()"></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-container>
              <v-row>
                <v-spacer></v-spacer>
                <v-col cols="3" sm="3" md="3">
                  <v-btn color="primary" large type="submit" class="float-right" :disabled="!valid || loading">Guardar</v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-card-actions>
        </v-form>
      </v-card>
    </template>
  </div>
</template>

<script>
import Products from '@/apiClient/Products'
import Budgets from "@/apiClient/Budgets"

export default {
  data() {
    return {
      products: Products,
      budgets: Budgets,
      productsList: [],
      valid: true,
      loading: false,
      model: {
        customer: '',
        address: '',
        city: '',
        seller: '',
        productsBudget: [],
        total: ''
      },
      defaultItem: {
        customer: '',
        address: '',
        city: '',
        seller: '',
        productsBudget: [],
        total: ''
      },
      rules: {
        required: value => !!value || 'Campo obligatorio.',
        maxChar: v => v.length <= 200 || 'Este campo no debe superar los 200 caracteres',
      },
    }
  },
  async created() {
    this.budgets = new Budgets();
    this.products = new Products();
    await this.products.getProducts().then(res => {
      this.productsList = res.data
    })
  },
  methods: {
    formatedLabel(item){
      return `${item.code} | ${item.description} ${item.size} | $${item.price}`
    },
    addProduct(){
      this.product
      this.model.productsBudget.push({
        product: '',
        id: '',
        quantity: 1,
        price: '',
        subtotal: ''
      })
    },
    deleteProduct(index){
      this.model.productsBudget.splice(index, 1);
    },
    addProductToBudget(index, product) {
      this.model.productsBudget[index].id = product.id
      this.model.productsBudget[index].price = product.price
    },
    calculateSubtotal(index, item) {
      if(item.id){
        let subtotal = parseFloat(item.price) * item.quantity 
        this.model.productsBudget[index].subtotal = subtotal
        return subtotal
      }
      return ''
    },
    calculateTotal() {
      if(this.model.productsBudget.length > 0){
        let total = 0
        this.model.productsBudget.forEach(val => {
          total += val.subtotal
        })
        this.model.total = total
        return total
      }
      return ''
    },
    async save() {
      this.loading = true
      if(this.$refs.form.validate()){
        if(this.model.productsBudget.length === 0) {
          this.$swal({
            icon: 'error',
            title: 'Debe agregar productos',
          })
          this.loading = false
          return
        }
        this.model.productsBudget.forEach(element => {
          if(!element.id){
            this.$swal({
              icon: 'error',
              title: 'Debe seleccionar un producto',
            })
            this.loading = false
            return
          }
        })
        await this.budgets.createBudget(this.model)
          .then(res => {
            this.$swal({
              icon: 'success',
              title: res.data.message,
            })
            this.loading = false
            this.model = this.defaultItem
          })
          .catch(err => {
            this.$swal({
              icon: 'error',
              title: 'Error al agregar el remito',
            })
            this.loading = false
          })
      }else{
        this.loading = false
      }
    }
  }
}
</script>