<template>
  <div class="orders-container">
    <template>
      <v-data-table :headers="headers" :items="orderList" class="elevation-1" locale="es">
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>Listado de Ordenes</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>
                <v-form ref="form" v-model="valid" @submit.prevent="save" lazy-validation>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-select
                            v-model="editedItem.order_status"
                            :items="statusList"
                            item-id="id"
                            item-value="id"
                            item-text="name"
                            label="Estado"
                            :rules="[rules.required]"
                          ></v-select>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field v-model="editedItem.order_tracking_number" label="N de tracking"></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-textarea v-model="editedItem.order_comment" label="Observaciones"></v-textarea>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                    <v-btn color="blue darken-1" text type="submit" :disabled="!valid || loading">Guardar</v-btn>
                  </v-card-actions>
                </v-form>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.budget_order="{ item }">
          <template v-if="item.budget_order">
            <v-simple-checkbox :value="true" disabled></v-simple-checkbox>
          </template>
          <template v-else>
            <v-simple-checkbox :value="false" disabled></v-simple-checkbox>
          </template>
        </template>
        <template v-slot:item.actions="{ item }">
          <template v-if="!item.budget_order">
            <v-icon small class="mr-2" @click="editItem(item)">mdi-plus-thick</v-icon>
          </template>
          <template v-else>
            <v-icon small class="mr-2" @click="openDetail(item)">mdi-eye</v-icon>
            <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
            <!-- <v-icon small @click="deleteItem(item)">mdi-delete</v-icon> -->
          </template>
        </template>
      </v-data-table>
    </template>
    <v-dialog v-model="dialogDetail" max-width="50rem">
      <v-card>
        <v-card-title>
          <span class="headline">Detalle de la orden</span>
          <v-spacer></v-spacer>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="12" md="12">
                <v-select
                  v-model="orderDetail.order_status"
                  :items="statusList"
                  item-id="id"
                  item-value="id"
                  item-text="name"
                  label="Estado"
                  readonly
                ></v-select>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" sm="12" md="12">
                <v-text-field v-model="orderDetail.order_tracking_number" label="N de tracking" readonly></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" sm="12" md="12">
                <v-textarea v-model="orderDetail.order_comment" label="Observaciones" readonly></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import Orders from "@/apiClient/Orders";

export default {
  data() {
    return {
      orders: Orders,
      orderList: [],
      statusList: [
        {
          id: 1,
          name: 'En espera'
        },
        {
          id: 2,
          name: 'En proceso de armado'
        },
        {
          id: 3,
          name: 'Listo'
        },
        {
          id: 4,
          name: 'Enviado'
        }
      ],
      dialog: false,
      headers: [
        {
          text: "Fecha de remito",
          sortable: true,
          value: "budget_date"
        },
        { text: "Cliente", value: "budget_customer", sortable: true },
        { text: "Vendedor", value: "budget_seller" },
        { text: "Total", value: "budget_total" },
        { text: "Tiene orden?", value: "budget_order"},
        { text: '', value: 'actions', sortable: false },
      ],
      valid: true,
      loading: false,
      editedIndex: -1,
      editedItem: {
        budget_id: 0,
        budget_date: "",
        budget_customer: "",
        budget_seller: "",
        budget_total: "",
        order_id: null,
        budget_order: null,
        order_date: null,
        order_status: null,
        order_comment: null,
        order_tracking_number: null
      },
      defaultItem: {
        budget_id: 0,
        budget_date: "",
        budget_customer: "",
        budget_seller: "",
        budget_total: "",
        order_id: null,
        budget_order: null,
        order_date: null,
        order_status: null,
        order_comment: null,
        order_tracking_number: null
      },
      rules: {
        required: value => !!value || 'Campo obligatorio.',
        maxDesc: v => v.length <= 200 || 'Este campo no debe superar los 200 caracteres',
        maxSize: v => v.length <= 15 || 'Este campo no debe superar los 15 caracteres',
      },
      dialogDetail: false,
      orderDetail: {}
    };
  },
  async created() {
    this.orders = new Orders()
    await this.getOrders()
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nueva orden" : "Editar orden";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  methods: {
    getOrders(){
      this.orders.getOrders()
        .then(res => {
          this.orderList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    openDetail(item){
      this.orderDetail = item
      this.dialogDetail = true
    },
    editItem(item) {
      if(item.budget_order){
        this.editedIndex = this.orderList.indexOf(item);
      }
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    // deleteItem(item) {
    //   this.$swal({
    //     title: 'Ests seguro?',
    //     icon: 'warning',
    //     showCancelButton: true,
    //     confirmButtonText: 'Si, eliminar',
    //     cancelButtonText: 'No, cancelar',
    //     reverseButtons: true
    //   }).then((result) => {
    //     if (result.value) {
    //       this.orders.deleteOrder(item.id).then(res => {
    //         this.getOrders()
    //         this.$swal({
    //           icon: 'success',
    //           title: res.data.message,
    //         })
    //       }).catch(err => {
    //         this.$swal({
    //           icon: 'error',
    //           title: res.data.message,
    //         })
    //       })
    //     }
    //   })
    // },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async save() {
      this.loading = true
      if(this.$refs.form.validate()){
        if (this.editedIndex > -1) {
          await this.orders
            .updateOrder(this.editedItem)
            .then(res => {
              this.loading = false
              this.getOrders()
              this.$swal({
                icon: 'success',
                title: res.data.message,
              });
            })
            .catch(err => {
              this.loading = false
              this.$swal({
                icon: 'error',
                title: 'Error',
              });
            });
        } else {
          await this.orders
            .createOrder(this.editedItem)
            .then(res => {
              this.loading = false
              this.getOrders()
              this.$swal({
                icon: 'success',
                title: res.data.message,
              });
            })
            .catch(err => {
              this.loading = false
              this.$swal({
                icon: 'error',
                title: 'Error',
              });
            });
        }
        this.close();
      }else{
        this.loading = false
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.orders-container {
  padding: 2rem 2rem 0 2rem;
}
</style>